@import "helpers/mixins";

.pubHead {
  @include breakpoints(min-large) {
    padding-top: 5vw;
  }

  @include breakpoints(min-xlarge) {
    padding: 10vw 10vw 0;
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 4vw;
    position: relative;

    @include breakpoints(mobile) {
      gap: 8vw;
      flex-direction: column !important ;
    }

    &::before {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      width: 150px;
      aspect-ratio: 0.9;
      z-index: -1;
      background: {
        image: url("../assets/images/all_pages/newBgs1.webp");
        size: contain;
        repeat: no-repeat;
      }

      @media (min-width: 1800px) {
        inset: 80% 0;
      }

      @include breakpoints(mobile) {
        top: 50%;
      }
    }

    .text {
      flex-basis: 65%;
      h2 {
        font-size: 1.7rem;
        margin-bottom: 3.5vh;
        @include breakpoints(min-xlarge) {
          font-size: 2.5rem;
        }
      }

      p {
        &:not(:last-child) {
          margin-bottom: 2.5vh;
        }
      }
    }

    .poster {
      flex-basis: 35%;
      position: relative;

      @include breakpoints(mobile) {
        order: -1;
      }
      img {
        border-radius: 100px 0;
        filter: drop-shadow(4px 5px 10px grey);
        animation: up-Down 1.2s ease-in-out infinite
          alternate;
        position: unset !important;
        max-width: 100% !important;

        &:hover {
          filter: drop-shadow(
            0 0.2rem 0.25rem var(--alt-clr)
          );
          transform: scale(1.1);
        }

        @include breakpoints(mobile) {
          max-width: 50vw !important;
        }
      }
    }
  }

  .cardBox {
    max-height: unset;
  }
}

.pubSwiper {
  overflow: unset;

  .swiper-wrapper .swiper-slide {
    height: unset !important;
    width: fit-content !important;
  }

  .swiper-button-next-unique,
  .swiper-button-prev-unique {
    position: absolute;
    top: 50%;
    width: calc(var(--swiper-navigation-size) / 44 * 27);
    height: var(--swiper-navigation-size);
    margin-top: calc(
      0px - (var(--swiper-navigation-size) / 2)
    );
    z-index: 10;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(
      --swiper-navigation-color,
      var(--swiper-theme-color)
    );
  }
  .swiper-button-next-unique,
  .swiper-rtl .swiper-button-prev-unique,
  .swiper-button-prev-unique,
  .swiper-rtl .swiper-button-next-unique {
    position: absolute;
    top: 50%;
    right: -30%;
    transform: translate(-50%, -50%);

    @media (min-width: 1800px) {
      right: -15%;
    }
  }
  .swiper-button-next-unique:after,
  .swiper-button-prev-unique:after {
    font-family: swiper-icons;
    font-size: var(--swiper-navigation-size);
    text-transform: none !important;
    letter-spacing: 0;
    font-variant: initial;
    line-height: 1;
    color: var(--alt-clr);
  }
  .swiper-button-next-unique:after,
  .swiper-rtl .swiper-button-prev-unique:after {
    content: "next";
  }
  .swiper-button-prev-unique,
  .swiper-rtl .swiper-button-next-unique {
    left: -20%;
    right: auto;

    @media (min-width: 1800px) {
      left: -10%;
    }
  }
  .swiper-button-next-unique.swiper-button-disabled,
  .swiper-button-prev-unique.swiper-button-disabled {
    opacity: 0.35;
    cursor: auto;
    pointer-events: none;
  }
  .swiper-button-prev-unique:after,
  .swiper-rtl .swiper-button-next-unique:after {
    content: "prev";
  }
}

.publish-page .swp {
  @media (min-width: 577px) {
    margin-bottom: 5rem;
  }
}

.pubSrvs {
  h2 {
    font-size: 3rem;
    color: #988f8f;
    margin-bottom: 3vh;
    letter-spacing: 20px;
    animation: shadow-text 2s linear infinite alternate;
  }

  .srvs {
    display: flex;
    width: 70%;
    margin: auto;
    justify-content: space-between;
    align-items: flex-end;

    @include breakpoints(max-large) {
      width: unset;
    }

    @include breakpoints(mobile) {
      flex-direction: column;
      align-items: center;
      gap: 3rem;
    }
    .srv {
      display: grid;
      gap: 0.6rem;

      img {
        max-width: 150px;
        animation: up-Down 1s ease-in-out infinite alternate;
        place-self: center;

        @include breakpoints(max-large) {
          max-width: 100px;
        }
      }
      &:hover img {
        animation-play-state: paused;
      }
      &:hover {
        filter: drop-shadow(
          0 0.2rem 0.25rem var(--alt-clr)
        );
        transform: scale(1.1);
      }

      h3 {
        place-self: center;
      }
    }
  }
}
.publish-page {
  .contact-form {
    @include breakpoints(large) {
      margin: 10rem auto;
      overflow: unset !important;
    }

    @include breakpoints(mobile) {
      padding: 5rem 0;
    }

    .box .header img {
      position: relative;
      top: -12px;

      @include breakpoints(max-medium) {
        max-width: 50px;
        top: -3px;
      }
    }

    @include breakpoints(medium) {
      .box .form {
        top: unset;
      }
    }

    @include breakpoints(medium) {
      .container {
        .box {
          top: 100px;

          .header {
            bottom: unset;
          }
        }
      }
    }

    &.alt-form {
      margin: 0 auto;
      .box {
        position: unset;
        background: linear-gradient(
          0deg,
          hsla(0, 0%, 69%, 1) 0%,
          hsla(0, 0%, 100%, 1) 52%
        );
        padding: 3rem 3rem 0;
        width: 80%;
        border-radius: 70px 0;
        filter: drop-shadow(0.5rem 0.5rem 0.6rem grey);

        @include breakpoints(min-xlarge) {
          max-width: 30vw;
        }

        input:not(input[type="submit"]):not(#formFile),
        textarea,
        .fileInp {
          background: linear-gradient(
            0deg,
            hsla(0, 0%, 100%, 1) 60%,
            #61c3ff40 100%
          );
          border-bottom-right-radius: 50px;
        }

        .fileInp {
          padding: 0.7rem 0.5rem 1rem;

          #formFile {
            background-color: transparent;
          }
        }

        input[type="submit"] {
          transform: translateY(50%);
          padding: 0.2rem 1rem !important;
          border-radius: 25px 0;
          background-color: var(--bg-clr);
        }

        .form {
          input,
          textarea {
            padding: {
              left: 1rem;
              right: 1rem;
            }
          }
        }

        .radios {
          text-align: left;
          background: linear-gradient(
            0deg,
            hsla(0, 0%, 100%, 1) 60%,
            #61c3ff40 100%
          );
          border-bottom-right-radius: 50px;
          padding: 0.6rem 1rem;
          width: 100%;

          p {
            font-size: 1.2rem;
            margin-bottom: 0.7rem;
          }
          .rad {
            &:not(:last-child) {
              margin-bottom: 0.45rem;
            }
            input[type="radio"] {
              appearance: auto;
              display: inline-block;
              margin-bottom: unset;
              width: fit-content;
            }
            label {
            }
          }
        }
      }
    }
  }
}
// publish
.togglerContent {
  margin: 10rem auto;

  @include breakpoints(mobile) {
    margin-bottom: 0;
  }

  .head {
    display: flex;
    align-items: center;
    gap: 2rem;

    @include breakpoints(max-large) {
      margin-bottom: 3rem;
    }

    h2 {
      font-size: 2.5rem;
      padding-left: 2rem;
    }
    img.icon {
      max-width: 150px;
      place-self: center;

      @include breakpoints(max-large) {
        max-width: 100px;
      }
    }
    &:hover {
      filter: drop-shadow(0 0.2rem 0.25rem var(--alt-clr));
    }
  }

  .head ~ p {
    margin: auto;
    @include breakpoints(min-xlarge) {
      width: 70%;
    }
  }

  button.specBtn {
    background-color: #f1d9d6;
  }
  button.specBtn {
    position: absolute;

    left: 50%;
    transform: translateX(-50%);

    &.clk {
      bottom: 0;
      transform: translate(-50%, 50%);
      background-color: var(--bg-clr);
    }
  }

  .products {
    margin-top: 3rem;

    .productBoxes {
      display: flex;
      gap: 2rem;
      padding-top: 5rem;

      @include breakpoints(max-large) {
        flex-direction: column;
        gap: 4rem;
        width: 70%;
        margin: auto;
      }

      .box {
        position: relative;
        background-color: #f1d9d6;
        flex: 1;
        padding: 1.3rem;
        box-shadow: -5px -5px 15px grey;
        animation: up-Down 1.2s ease-in-out infinite
          alternate paused;

        &:nth-of-type(even) {
          animation-play-state: running;
          @include breakpoints(min-xlarge) {
            top: 10px;
          }
        }

        ul {
          list-style: circle;
          padding-left: 1rem;
        }

        h3 {
          margin-bottom: 2rem;
          font: {
            size: 1.6rem;
            weight: bold;
          }
        }
      }
    }
  }
}

// translate
.togglerContent {
  &[data-section="translate"] {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 15vw;
      bottom: 0;
      width: 150px;
      aspect-ratio: 0.9;
      z-index: -1;
      background: {
        image: url("../assets/images/all_pages/newBgs1.webp");
        size: contain;
        repeat: no-repeat;
      }
    }
  }
  .parags {
    margin-top: 3rem;
    .para {
      position: relative;
      background-color: #f1d9d6;
      box-shadow: -5px -5px 15px grey;
      padding: 1.3rem;

      &:not(:last-child) {
        margin-bottom: 4rem;
      }

      h3 {
        margin-bottom: 1.6rem;
        font-size: 1.6rem;
        font-weight: bold;
      }
      p {
      }
      button {
        left: 80%;

        @include breakpoints(mobile) {
          left: 50%;
        }
      }
    }
  }
}

// distribute
.togglerContent[data-section="distribute"] {
  .products {
    .productBoxes {
      flex-direction: column;
      @include breakpoints(max-large) {
        width: unset;
      }
      .box {
        margin-bottom: 4rem;
        &:first-child,
        &:nth-child(2) {
          max-width: 60%;

          @include breakpoints(mobile) {
            max-width: 85%;
          }
        }

        &:nth-child(2) {
          align-self: end;
        }
        &:nth-child(even) {
          animation-play-state: paused;
        }
      }
    }
  }
}

// prize
.togglerContent[data-section="prize"] {
  .parags {
    margin-top: unset;
    position: relative;

    @include breakpoints(min-large) {
      padding-left: 10vw;
      top: -2vw;
    }

    p {
      &:not(:last-child) {
        margin-bottom: 2.5vh;
      }
    }

    .prices {
      position: relative;
      &::before {
        content: "";
        position: absolute;
        left: -12vw;
        bottom: 30%;
        width: 150px;
        aspect-ratio: 0.9;
        z-index: -1;
        background: {
          image: url("../assets/images/all_pages/newBgs1.webp");
          size: contain;
          repeat: no-repeat;
        }
        @include breakpoints(mobile) {
          opacity: 0.6;
        }
      }
    }
  }

  .alt-form {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      width: 25vw;
      aspect-ratio: 0.6;
      transform: scaleX(-1) translateY(50%);
      z-index: -1;
      background: {
        image: url("../assets/images/all_pages/background2-01.webp");
        size: contain;
        repeat: no-repeat;
      }
    }
  }
}

// read
.read-page,
.writer-page {
  .channelsBanner {
    margin-bottom: 10vw;
    padding: 0;
  }

  .pubHead {
    padding: unset;
  }

  .defIcon {
    &::before {
      content: "";
      position: absolute;
      right: 10vw;
      width: calc(min(150px, 10vw));
      aspect-ratio: 0.9;
      z-index: -1;
      background: {
        image: url("../assets/images/all_pages/newBgs1.webp");
        size: contain;
        repeat: no-repeat;
      }

      @include breakpoints(mobile) {
        opacity: 0.6;
      }
    }
    .def {
      img {
        max-width: calc(max(150px, 10vw));
      }
    }
  }

  .bookShower {
    position: relative;

    @include breakpoints(mobile) {
      overflow-x: hidden;
    }
    img.bg {
      position: absolute;
    }

    .swp {
      margin: {
        top: unset;
        bottom: 5rem;
      }
      width: fit-content;
      transform: translate(410px, 50px);

      @include breakpoints(mobile) {
        width: 100vw;
        transform: translate(60px, -20px);
      }

      @include breakpoints(min-medium) {
        transform: translate(390px, 18px);
      }
      @include breakpoints(min-large) {
        transform: translate(390px, 40px);
      }

      @include breakpoints(min-xlarge) {
        transform: translate(50%, 19%);
      }
      @media (min-width: 1800px) {
        transform: translate(58%, 40%);
      }
      .cardBox {
        max-height: unset;
        .sweeper .swiper {
          .swiper-slide {
            width: calc(min(250px, 15vw));
            aspect-ratio: 0.7;
            height: unset;
            img {
              max-width: unset;
            }
            &.swiper-slide-active {
              img {
                transform: scale(1.1);
              }

              .swiper-button-prev,
              .swiper-rtl .swiper-button-next,
              .swiper-button-next,
              .swiper-rtl .swiper-button-prev {
                display: initial;
              }
            }
          }
        }
      }

      .swiper-button-prev,
      .swiper-rtl .swiper-button-next {
        left: -50px;
        right: auto;
        color: var(--theme-clr);
        display: none;
      }
      .swiper-button-next,
      .swiper-rtl .swiper-button-prev {
        right: -50px;
        left: auto;
        display: none;
        color: var(--theme-clr);
      }
    }
  }

  .fisP {
    padding-top: 5rem;

    @media (min-width: 1800px) {
      padding-top: 15rem;
    }
  }

  .writings {
    margin: 8rem auto;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      width: calc(min(30vw, 300px));
      transform: scaleX(-1) translateY(30%);
      aspect-ratio: 0.4;
      z-index: -2;
      background: {
        image: url("../assets/images/all_pages/newBgs2.webp");
        size: contain;
        repeat: no-repeat;
      }

      @include breakpoints(mobile) {
        opacity: 0.4;
        transform: scaleX(-1) translateY(150%);
      }
    }
    .container {
      .writingSec {
        display: flex;
        flex-direction: column;

        @include breakpoints(max-large) {
          gap: 5rem;
        }

        @include breakpoints(max-medium) {
          flex-wrap: wrap;
        }
        .sec {
          justify-content: flex-start;

          @include breakpoints(max-medium) {
            justify-content: center;
            flex-wrap: wrap;
          }

          &:nth-child(even) {
            gap: 2rem;
            @include breakpoints(max-large) {
              flex-direction: column;
            }
          }
          &:nth-child(odd) {
            gap: 15vw;
            flex-direction: row-reverse;
            @include breakpoints(max-medium) {
              gap: 10vw;
            }

            .text {
              gap: 3rem;
            }
          }
          img {
            max-width: 250px;
          }
          .text {
            display: flex;
            flex-direction: column;
            @include breakpoints(max-medium) {
              text-align: center;
            }

            h2 {
              margin-bottom: 1rem;
            }
            a {
              margin-top: 1rem;
              align-self: end;
              button.specBtn {
                width: fit-content;
                padding: {
                  left: 3rem;
                  right: 3rem;
                }
                background-color: var(--alt-clr);

                &:hover {
                  background-color: white;
                }
              }
            }
          }
        }
      }
    }
  }
}

// writer
.writer-page {
  .writings {
    &::before {
      content: none;
    }

    .container {
      .writingSec {
        .sec {
          @include breakpoints(max-large) {
            flex-direction: column !important;
            gap: 2rem !important;
          }
          .text {
            gap: unset !important;
          }
        }
      }
    }
  }

  .posts {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      right: 0;
      bottom: 30%;
      width: 15vw;
      aspect-ratio: 0.6;
      transform: scaleX(-1) translateY(50%);
      z-index: -1;
      background: {
        image: url("../assets/images/all_pages/background2-01.webp");
        size: contain;
        repeat: no-repeat;
      }
    }
    .container {
      .postsCont {
        display: flex;
        flex-direction: column;
        gap: 4rem;
        .post {
          display: flex;
          flex-direction: column;
          align-items: center;
          @include breakpoints(min-xlarge) {
            flex-direction: row;

            &:nth-child(odd) {
              flex-direction: row-reverse;

              img {
                transform: translate(-50%, -20%);
              }
              .text {
                justify-content: flex-start;
              }
            }
          }
          img {
            max-width: 300px;
            z-index: 99;
            @include breakpoints(min-xlarge) {
              transform: translate(50%, -20%);
            }
          }

          .text {
            text-align: center;
            background-color: var(--bg-clr);
            padding: 1rem 1rem 4rem;
            border-radius: 30px;
            box-shadow: -6px 4px 20px grey;
            position: relative;
            display: flex;
            justify-content: flex-end;

            @include breakpoints(min-xlarge) {
              padding: 1rem 2rem 4rem;

              p {
                width: 80%;
              }
            }

            a {
              position: absolute;
              bottom: 0;
              left: 50%;
              transform: translate(-50%, 50%);
              button {
                background-color: var(--alt-clr);
                padding: 0.1rem 3rem;
              }
            }
          }
        }
      }
    }
  }
}

body.arabic {
  .pubHead {
    .content {
      flex-direction: row-reverse;
    }
    .text {
      text-align: right;
    }
  }

  .contact-form {
    #inp_name2,
    #inp_mail2 {
      direction: rtl;
    }
  }

  .contact-form.alt-form .box .radios {
    text-align: right;
  }

  .productBoxes,
  .parags {
    text-align: right;
    direction: rtl;

    ul {
      padding-right: 1rem;
    }
  }

  .togglerContent {
    .contact-form.alt-form .box .fileInp,
    .head,
    .radios,
    #inp_msg2,
    .head ~ p {
      direction: rtl;
    }
    .parags {
      button {
        @include breakpoints(min-xlarge) {
          left: auto;
          right: 80%;
        }
      }
    }

    &[data-section="prize"] {
      .parags {
        padding-left: unset;
        padding-right: 10vw;

        .prices {
          &::before {
            left: auto;
            right: -12vw;
          }
        }
      }
    }
  }

  .defIcon::before {
    left: 10vw;
    right: auto;
  }

  .fisP {
    direction: rtl;
  }

  .pubSrvs h2 {
    font-size: 4rem;
    letter-spacing: unset;
  }
}

.swp {
  margin-bottom: 5rem;
}
